.App {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(ellipse at center top, #1f3756, #141539);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  color: white;
  font-family: "Barlow Semi Condensed", sans-serif;
  overflow: hidden;
}

.App .dark__cover {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.412);
  z-index: 2;
}

.App .image__preview {
  position: absolute;
  margin: 0;
  width: 100%;
  height: 100%;
